<template>
  <div class="tax-page home">
    <h4>参数设置</h4>
    <div class="areas">
      <div class="item"><span class="-title">您的区域：</span><span class="-name">{{user.areaName||'未设置'}}</span>
        <a class="-ops" @click.stop="changeArea">{{user.areaName?'更换':'设置'}}</a>
      </div>
      <div class="item">
        <span class="-title">计税参数：</span>
        <div class="param-settings">
          <div class="item" v-for="param in params">
            <span>{{param.param.name}}</span>
            <el-input v-model="param.config" placeholder="请输入" maxlength="10" @change="changeParam(param)">
              <template slot="append">{{param.param.unit}}</template>
            </el-input>
          </div>
        </div>
        <el-button type="primary" @click="saveParam">保存</el-button>
      </div>
    </div>
    <h4>订阅服务</h4>
    <div class="subs">
      <div class="apis" v-if="subs.length > 0" v-loading="loading">
        <div class="api" v-for="sub in subs" :key="sub.id">
          <div class="-inner">
            <div class="-icon">{{sub.storeName}}</div>
            <div class="-name">{{sub.apiName}}</div>
            <div class="-time">
              <span class="-type">{{sub.payStateName}}</span>
              <span>{{sub.endDate}}到期</span>
            </div>
          </div>
        </div>
      </div>
      <div class="more" v-if="subs.length > 0">还可以订阅如下服务</div>
      <div class="unsub-apis" v-if="unApis.length >0">
        <div class="api" v-for="api in unApis" :key="api.id">
          <div class="-icon">{{api.storeName}}</div>
          <div class="-info">
            <div class="-ops">
              <span class="-name">{{api.apiName}}</span>
              <span class="-price"><span class="-p1">￥</span><span class="-p2">{{api.price}}</span><span
                  class="-p3">/年</span></span>
              <a>订阅</a>
            </div>
            <div class="-desc">{{api.descs}}</div>
          </div>
        </div>
      </div>
    </div>
    <h4>客户端下载</h4>
    <div class="locals">
      <div class="item">
        <div class="image">包</div>
        <div><a>下载安装包</a></div>
      </div>
      <div class="item">
        <div class="image">看</div>
        <div><a>下载指导文档</a></div>
      </div>
      <div class="item">
        <div class="image">问</div>
        <div><a>在线提问</a></div>
      </div>
    </div>
    <h4>客户端更新</h4>
    <div class="upgrades">
      <div class="options">
        <span>在客户端中的软件更新菜单可以下载安装更新包，自动检测需要安装的软件包</span>
      </div>
      <table class="table sample-table table-bordered table-hover">
        <thead>
        <tr>
          <th scope="col">版本号</th>
          <th scope="col">更新日期</th>
          <th scope="col">jar包</th>
          <th scope="col">描述</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>PTMS-1.0.0</td>
          <td>2018年9月12日</td>
          <td>PTMS-1.0.0-2.jar</td>
          <td>修复客户端人员信息设置错误</td>
        </tr>
        <tr>
          <td>PTMS-1.0.0</td>
          <td>2018年8月12日</td>
          <td>PTMS-1.0.0-1.jar</td>
          <td>客户端升级</td>
        </tr>
        </tbody>
      </table>
    </div>
    
    <area-select :visible.sync="areaDialog.visible" @select:area="selectArea"></area-select>
  </div>
</template>

<script>
  import SubscribeApi from '@/api/manage/subs';
  import UserApi from '@/api/manage/user';
  import ParamApi from '@/api/manage/param';
  import AreaSelect from "../../ui/components/area/areaSelect";

  export default {
    components: {AreaSelect},
    name: "home",
    data() {
      return {
        apis: [],
        subs: [],
        user: this.$store.getters.user.info,
        areaDialog: {
          visible: false
        },
        params: []
      }
    },
    mounted() {
      this.$emit('layout:load', "home")
      this.loadApis();
      this.loadSubs();
      this.showArea();
      this.loadParams();
    },
    computed: {
      unApis: function () {
        var unApis = [];
        this.apis.forEach(api => {
          var found = false;
          for (var i = 0; i < this.subs.length; i++) {
            if (api.apiIdent == this.subs[i].apiIdent) {
              found = true;
              break;
            }
          }
          if (!found) {
            unApis.push(api);
          }
        })
        return unApis;
      }
    },
    methods: {
      showArea: function () {
        if (!this.user.areaId) {
          this.areaDialog.visible = true;
        }
      },
      loadParams: function () {
        ParamApi.customs().then(res => {
          this.params = res.obj
        })
      },
      loadApis: function () {
        SubscribeApi.apis().then(res => {
          this.apis = res.obj || [];
        })
      },
      loadSubs: function () {
        SubscribeApi.subs().then(res => {
          this.subs = res.obj || [];
        })
      },
      changeArea: function () {
        this.areaDialog.visible = true;
      },
      selectArea: function (area) {
        UserApi.saveArea(area).then(res => {
          this.$store.dispatch('GetUser').then(res => {
            this.user = this.$store.getters.user.info;
          })
        })
      },
      changeParam: function (param) {
        param.config = parseFloat(param.config)
      },
      saveParam: function () {
        var _p = [];
        this.params.forEach(function (e) {
          _p.push({
            id: e.id,
            config: e.config
          })
        })
        var json = JSON.stringify(_p)
        ParamApi.save(json).then(res => {
          this.$notify.success("修改成功！")
        })
      }
    }
  }
</script>

