import fetch from '../fetch'

/**
 * Version service
 */
const Param = {
  customs: () => fetch({
    url: '/api/user/param/customs',
    method: 'get'
  }),
  save: (params) => fetch({
    url: '/api/user/param/save',
    method: 'post',
    data:{json_params: params}
  })
};

/**
 * export service
 */
export default Param