<template>
  <div class="user-table-select">
    <el-dialog
        title="请选择区域"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="area-selected">选择城市：{{area.name}}</div>
      <div class="area-settings">
        <div class="provinces -settings">
          <div class="-name">省</div>
          <div class="-items">
            <swak-link @link:click="provinceSelect" :id="province.id" :index="index" clazz="-item"
                       :class="s1 == province.id?'selected':''"
                       v-for="(province, index) in provinces" :key="province.id">{{province.name}}
            </swak-link>
          </div>
        </div>
        <div class="citys -settings">
          <div class="-name">市</div>
          <div class="-items">
            <swak-link @link:click="citySelect" :id="city.id" :index="index" clazz="-item"
                       :class="s2 == city.id?'selected':''"
                       v-for="(city,index) in citys" :key="city.id">{{city.name}}
            </swak-link>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
          <el-button @click="doCancel">取 消</el-button>
          <el-button type="primary" @click="doConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import AreaApi from '@/api/system/area';
  import SwakLink from "../link/link";

  export default {
    components: {SwakLink},
    name: "area-select",
    data() {
      return {
        provinces: [],
        citys: [],
        s1: '',
        s2: '',
        area: {}
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      visible: function () {
        if (this.visible) {
          this.loadProvinces();
        }
      }
    },
    methods: {
      loadProvinces: function () {
        AreaApi.provinces().then(data => {
          this.provinces = data.obj;
        })
      },
      loadCitys: function (id) {
        AreaApi.citys(id).then(data => {
          this.citys = data.obj;
        })
      },
      provinceSelect: function (e) {
        var $a = e.target;
        var id = $a.getAttribute("id");
        var index = $a.getAttribute("index");
        this.s1 = id;
        this.area = this.provinces[index]
        this.loadCitys(id);
      },
      citySelect: function (e) {
        var $a = e.target;
        var id = $a.getAttribute("id");
        var index = $a.getAttribute("index");
        this.s2 = id;
        this.area = this.citys[index]
      },
      doCancel: function () {
        this.$emit('update:visible', false)
      },
      doConfirm: function() {
        this.selectArea();
        this.doCancel();
      },
      selectArea: function () {
        this.$emit('select:area', this.area);
      }
    }
  }
</script>

