        background-color $color-border-xxx;
        border-bottom: 1px solid $color-border;
      }
      .-items {
        height 450px;
        overflow-y scroll;
        .-item {
          display block
          padding 10px;
          border-bottom: 1px solid $color-border;
          &:hover {
            background-color $color-border-xxx;
          }
        }
        .selected {
          background-color $color-border-xxx;
        }
      }
    }
    .citys {
      margin-right: 0;
      margin-left: 5px;
    }
  }
</style>